@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
div.default-input,div.default-select{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-bottom:30px;position:relative}div.default-input label,div.default-select label{font-family:"Roboto",sans-serif;font-size:18px;line-height:22px;color:#555453;margin-bottom:5px;transition:width .3s ease-in-out}div.default-input input,div.default-input select,div.default-select input,div.default-select select{width:100%;height:40px;border-radius:0;font-family:"Roboto",sans-serif;font-size:18px;line-height:22px;color:#000;border:none;border-bottom:1px solid #555453;padding:2px 10px}div.default-input input:focus+div.line,div.default-input select:focus+div.line,div.default-select input:focus+div.line,div.default-select select:focus+div.line{width:100%;transition:width .3s ease-in-out}div.default-input .line,div.default-select .line{content:"";display:block;position:absolute;width:0;height:2px;bottom:0;left:0;right:0;background-color:#000;transition:width .2s ease-in-out;margin:0 auto}

button.btn-default{position:relative;width:100%;height:50px;background-color:#58ff78;border:none;border-radius:4px;font-family:"Roboto",sans-serif;font-size:18px;line-height:22px;color:#2D2D2D;font-weight:700;transition:box-shadow .3s ease-in-out;max-width:100%}button.btn-default:hover{box-shadow:0 0 16px rgba(88,255,120,0.7);transition:box-shadow .3s ease-in-out}button.btn-default.mb-30{margin-bottom:30px}button.btn-icon{width:38px;height:38px;background-color:#555453;border-radius:50%;border:none}button.btn-icon.edit{transition:background-color .3s ease-in-out}button.btn-icon.edit:hover{background-color:#3454d1;transition:background-color .3s ease-in-out}button.btn-icon.remove{transition:background-color .3s ease-in-out}button.btn-icon.remove:hover{background-color:#FF5E5B;transition:background-color .3s ease-in-out}

div.login-wrapper{background-color:#2D2D2D;width:100%;min-height:100vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}div.login-wrapper div.card-login{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}div.login-wrapper div.card-login .logo{-webkit-filter:invert(1);filter:invert(1);margin:0 auto 40px}div.login-wrapper div.card-login form{width:100%}div.login-wrapper div.card-login form button{margin-top:30px}

header{width:100%;background-color:#2D2D2D}header div.menu{height:90px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}header div.menu .logo{margin-right:60px;max-width:60%}@media screen and (max-width: 767px){header div.menu{-webkit-justify-content:space-between;justify-content:space-between}header div.menu .logo{margin-right:30px}header div.menu .logo img{max-width:100%}}header div.menu ul{display:-webkit-flex;display:flex}@media screen and (max-width: 767px){header div.menu ul{display:none}}header div.menu ul li.menu-item{cursor:pointer;margin-right:60px}header div.menu ul li.menu-item a{font-family:"Roboto",sans-serif;font-size:1.25rem;line-height:1.5rem;color:#fff;transition:color .3s ease-in-out}header div.menu ul li.menu-item:hover a{color:#d3c1b3;transition:color .3s ease-in-out}header div.menu div.user{margin-left:auto}header div.menu div.user div.user-img{display:block;width:50px;height:50px;border-radius:50%;background-color:#fff}header div.menu button.btn-hamburguer{display:none;width:42px;height:42px;background-color:transparent;border:0;cursor:pointer;padding:12px 8px}header div.menu button.btn-hamburguer svg{width:25px;height:17px}@media screen and (max-width: 767px){header div.menu button.btn-hamburguer{display:block}}header .menu-mobile{position:fixed;z-index:2;left:0;right:0;top:0;bottom:0;background-color:#000;-webkit-transform:translateX(100%);transform:translateX(100%);transition:-webkit-transform .4s ease-in-out;transition:transform .4s ease-in-out;transition:transform .4s ease-in-out, -webkit-transform .4s ease-in-out;padding:11px 0;display:none}@media screen and (max-width: 767px){header .menu-mobile{display:block}}header .menu-mobile.menu-open{-webkit-transform:translateX(0%);transform:translateX(0%);transition:-webkit-transform .4s ease-in-out;transition:transform .4s ease-in-out;transition:transform .4s ease-in-out, -webkit-transform .4s ease-in-out}header .menu-mobile .logo{display:block;margin-bottom:40px;max-width:60%}@media screen and (max-width: 767px){header .menu-mobile .logo img{max-width:100%}}header .menu-mobile button.btn-close{position:absolute;width:42px;height:42px;background-color:transparent;border:0;cursor:pointer;padding:12px 8px;right:16px;top:8px}header .menu-mobile button.btn-close svg{width:30px;height:30px}header .menu-mobile ul li.menu-item{cursor:pointer;margin-bottom:30px}header .menu-mobile ul li.menu-item a{font-family:"Roboto",sans-serif;font-size:2rem;line-height:2.25rem;color:#fff;transition:color .3s ease-in-out}header .menu-mobile ul li.menu-item:hover a{color:#d3c1b3;transition:color .3s ease-in-out}

.loader-wrapper{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.loader-wrapper .lds-dual-ring{display:inline-block;width:80px;height:80px}.loader-wrapper .lds-dual-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid #555453;border-color:#555453 transparent #555453 transparent;-webkit-animation:lds-dual-ring 1.2s linear infinite;animation:lds-dual-ring 1.2s linear infinite}@-webkit-keyframes lds-dual-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-dual-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

table.table-default{width:100%}table.table-default thead{border-bottom:3px solid #555453}table.table-default thead tr th{font-family:"Roboto",sans-serif;font-size:20px;line-height:24px;font-weight:700;padding:16px 0;vertical-align:middle}table.table-default thead tr th:first-of-type{width:100px}table.table-default thead tr th:last-of-type{text-align:center;width:200px}table.table-default tbody tr{border-bottom:1px solid #555453}table.table-default tbody tr td{font-family:"Roboto",sans-serif;font-size:16px;line-height:20px;font-weight:400;padding:10px 0;vertical-align:middle}table.table-default tbody tr td div.user-img{width:38px;height:38px;background-size:cover;background-position:center;background-repeat:no-repeat;background-color:#555453;border-radius:50%}table.table-default tbody tr td.actions{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}table.table-default tbody tr td.actions button{margin:0 8px}

.default-wrapper table.table-default{display:table}.default-wrapper #list-mobile{display:none;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}.default-wrapper #list-mobile div.row-user{width:48%;border:1px solid #000;border-radius:4px;padding:16px;margin-bottom:20px}.default-wrapper #list-mobile div.row-user div{margin-bottom:5px}.default-wrapper #list-mobile div.row-user div.user-img{display:block;margin:0 auto 30px;width:60px;height:60px;border-radius:5px;background-color:#000}.default-wrapper #list-mobile div.row-user div.actions{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}.default-wrapper #list-mobile div.row-user div.actions button{margin-left:10px;margin-right:0}@media screen and (max-width: 590px){.default-wrapper #list-mobile div.row-user{width:100%}}@media screen and (max-width: 1200px){.default-wrapper table.table-default thead tr th,.default-wrapper table.table-default tbody tr th{font-family:"Roboto",sans-serif;font-size:16px;line-height:20px;color:#000}.default-wrapper table.table-default thead tr th:first-of-type,.default-wrapper table.table-default tbody tr th:first-of-type{width:50px}.default-wrapper table.table-default thead tr th:last-of-type,.default-wrapper table.table-default tbody tr th:last-of-type{text-align:center;width:100px}.default-wrapper table.table-default thead tr td,.default-wrapper table.table-default tbody tr td{font-family:"Roboto",sans-serif;font-size:14px;line-height:20px;color:#000;padding-left:4px;padding-right:4px}}@media screen and (max-width: 767px){.default-wrapper table.table-default{display:none}.default-wrapper #list-mobile{display:-webkit-flex;display:flex}}

div.default-wrapper div.card h1.title{margin-bottom:40px}div.default-wrapper div.card form{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:60px}div.default-wrapper div.card form div.collumn{width:32%}div.default-wrapper div.card form div.collumn div.image-preview{width:200px;height:200px;background-color:#555453}div.default-wrapper div.card button.btn-default{width:400px;margin-left:auto;margin-right:0;display:block}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}a,a:focus,a:visited,button,button:focus,button:visited,div,div:focus,div:visited,textarea:focus,textarea:visited,input:focus,input:visited{outline-offset:0px;outline:none}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}img{-webkit-user-select:none;-o-user-select:none;-ms-user-select:none;user-select:none}a{text-decoration:none}body{position:relative;display:block;font-family:"Roboto",sans-serif;font-size:16px;line-height:20px;font-weight:400;color:#000;background-color:#555453}.default-wrapper{width:100%;display:block;position:relative;padding:32px 24px}.card{width:100%;background-color:#fff;border-radius:16px;padding:32px 24px}@media screen and (max-width: 767px){.card{padding:24px 8px}}.card.card-login{max-width:100%;margin:16px;width:450px;height:600px}@media screen and (max-width: 767px){.card.card-login{height:440px;padding:24px 26px}}.title{font-family:"Roboto",sans-serif;font-size:24px;line-height:28px;font-weight:700}

